html{overflow-x:hidden}body{overflow-x:hidden;padding:10px 0 0 0;margin:0;min-height:100vh;font-size:calc(16px + (26 - 16) * ((100vw - 300px) / (1600 - 300)));line-height:calc(1.4em + (2 - 1.4) * ((100vw - 300px)/(1600 - 300)))}section{position:absolute;height:100vh;width:100vw;color:#fff}section.stacked{position:fixed;top:0;left:0}section.current{z-index:-1}.section1{background:purple}.section2{background:red;top:100vh}.section3{background:green;top:200vh}.section3{background:#ff0;top:300vh}.section4{background:orange;top:400vh}.section5{background:brown;top:500vh}#fakeScrollbar{position:absolute;top:0;left:0;width:1px;z-index:-999;height:300vh;background:#000}.background{position:absolute;top:0;left:0;width:100%;height:100%}.background::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.66);z-index:1}.background video{object-fit:cover;width:100%;height:100%}.content{position:absolute;z-index:1;bottom:30%;left:5%;width:50%}h1{font-family:"Wes";font-size:300%;line-height:100%}